<template>
  <div id="app">
    <left-nav @setCarousel='handleClick'></left-nav>
    <carousel ref='carousel' :compData='allComponents'></carousel>
   </div>
</template>

<script>
import LeftNav from "@/components/LeftNav";
import Carousel from "@/components/Carousel";
import Home from '@/views/home'
import About from '@/views/about'
import Experience from '@/views/experience'
import Project from '@/views/project'
import Contact from '@/views/contact'

export default {
  name: "app",
  data(){
    return {
      allComponents:[
        {img:require('@/assets/images/carousel/h.jpg'),comp:Home},
        {img:require('@/assets/images/carousel/a.jpg'),comp:About},
        {img:require('@/assets/images/carousel/e.jpg'),comp:Experience},
        {img:require('@/assets/images/carousel/p.jpg'),comp:Project},
        {img:require('@/assets/images/carousel/c.jpg'),comp:Contact},
      ]
    }
  },
  components: {
    LeftNav,
    Carousel,
    Home,
    About,
    Experience,
    Project,
    Contact
  },
  methods: {
    handleClick(index) {
      // console.log(this.$refs.carousel)
      this.$refs.carousel.$children[0].setActiveItem(index);
      this.$store.commit('retIndex',index)
    }
  }
};
</script>

