import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentIndex:'0'
  },
  mutations: {
    addIndex(state,index) {
      state.currentIndex = index + 1 + '';
    },
    retIndex(state,index) {
      state.currentIndex = index+''
    }
  },
  actions: {
  },
  modules: {
  }
})
