<template>
  <el-carousel
    ref="carousel"
    arrow="never"
    height="100vh"
    :autoplay="false"
    indicator-position="none"
  >
    <el-carousel-item
      v-for="(item,index) in compData"
      :key="item.id"
      :style="{backgroundImage:`url(${item.img})`}"
    >
      <div class="content">
          <component :is="item.comp" @next="nextPage(index)"></component>
      </div>
    </el-carousel-item>
  </el-carousel>
</template>

<script>
export default {
  name: "Carousel",
  props: {
    compData: {
      type: Array
    }
  },
  data() {
    return {};
  },
  methods: {
    nextPage(index) {
      this.$refs.carousel.next();
      // console.log( typeof index)
      this.$store.commit("addIndex", index);
    }
  }
};
</script>

<style lang='less' scoped>
.el-carousel__item {
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 100% auto;
  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(37, 42, 48, 0.7);
  }
  .content {
    height: 450px;
    width: 70%;
    background-color: rgba(19, 21, 24, 0.8);
    position: fixed;
    right: 0;
    top: 50px;
    z-index: 99;
  }
}
</style>