<template>
  <div class="leftNav">
    <div class="elLeftNav-wraper">
      <h1>resume</h1>
      <el-menu
        :default-active="activeIndex"
        class="elLeftNav"
        background-color="transparent"
        text-color="#fff"
        active-text-color="#232323"
        @select="handleSelect"
      >
        <el-menu-item v-for="(item,index) in navs" :key="item.id" :index="index+''">
          <svg-icon :iconClass="item.icon"></svg-icon>
          <span slot="title">{{item.title}}</span>
        </el-menu-item>
      </el-menu>
    </div>
  </div>
</template>

<script>
export default {
  name: "LeftNav",
  data() {
    return {
      navs: [
        { title: "首页", icon: "home" },
        { title: "关于", icon: "about" },
        { title: "经验", icon: "experience" },
        { title: "项目", icon: "project" },
        { title: "联系", icon: "contact" }
      ]
    };
  },
  components: {},
  mounted() {},
  computed:{
    activeIndex(){
      return this.$store.state.currentIndex
    }
  },
  methods: {
    handleSelect(key, keyPath) {
      this.$emit("setCarousel", key);
    }
  }
};
</script>

<style lang='less' scoped>
.leftNav {
  width: 20%;
  height: 450px;
  position: fixed;
  left: 0;
  top: 50px;
  z-index: 9;
  background-color: rgba(19, 21, 24, 0.8);
  padding: 20px 25px 0;
  box-sizing: border-box;
  h1 {
    text-align: center;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 10px 1vw;
    border-radius: 15px;
    font-weight: 300;
    letter-spacing: 0.3vw;
    font-size: 3vw;
  }
  .elLeftNav.el-menu {
    border-right: none;
    padding-left: 10px;
    .el-menu-item {
      margin-bottom: 10px;
      width: 60%;
      font-size: 13px;
      letter-spacing: 1px;
      svg {
        margin-right: 15px;
        font-size: 15px;
      }
      &:nth-of-type(1) svg {
        font-size: 16px;
      }
      &:nth-of-type(2) svg {
        font-size: 13px;
      }
    }
    .el-menu-item:hover,
    .el-menu-item:focus {
      background-color: #fff !important;
      color: #232323 !important;
    }
    .el-menu-item.is-active {
      background-color: #fff !important;
    }
  }
}
</style>