<template>
  <div class="contact">
    <!-- 联系内容 -->
    <el-row :gutter="100">
      <el-col :span="12" class="form">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="contactForm">
          <el-form-item prop="userName">
            <el-input type="text" v-model="ruleForm.userName" placeholder="Your name..."></el-input>
          </el-form-item>
          <el-form-item prop="email">
            <el-input type="email" v-model="ruleForm.email" placeholder="Your email..."></el-input>
          </el-form-item>
          <el-form-item prop="subject">
            <el-input type="text" v-model="ruleForm.subject" placeholder="subject"></el-input>
          </el-form-item>
          <el-form-item prop="message">
            <el-input
              type="textarea"
              v-model="ruleForm.message"
              rows="6"
              placeholder="Your message"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">SEND NOW</el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="12" class="touch">
        <h2>keep in touch</h2>
        <ul class="touchContent">
          <li v-for="item in contactDatas" :key="item.id">
            <h5>{{item.title}}</h5>
            <svg-icon :iconClass="item.icon"></svg-icon>
            <span>{{item.content}}</span>
          </li>
        </ul>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "contact",
  data() {
    var validateUserName = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("请输入用户名"));
      } else {
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
      if (value === "") {
        callback(new Error("请输入邮箱"));
      } else if (!reg.test(value)) {
        callback(new Error("邮箱格式错误"));
      } else {
        callback();
      }
    };
    var validateSubject = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("请输入主题"));
      } else {
        callback();
      }
    };
    var validateMessage = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("请输入内容"));
      } else {
        callback();
      }
    };

    return {
      ruleForm: {
        userName: "",
        email: "",
        subject: "",
        message: ""
      },
      rules: {
        userName: [{ validator: validateUserName, trigger: "blur" }],
        email: [{ validator: validateEmail, trigger: "blur" }],
        subject: [{ validator: validateSubject, trigger: "blur" }],
        message: [{ validator: validateMessage, trigger: "blur" }]
      },
      contactDatas: [
        { title: "phone", icon: "phone", content: "18801054120" },
        { title: "email", icon: "email", content: "18801054120@163.com" },
        {
          title: "github",
          icon: "github",
          content: "https://github.com/shaoxinghua"
        }
      ]
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$message.success("发送成功");
        } else {
          this.$message.error("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

