<template>
  <div class="dialog">
    <el-dialog
      :title="dialog_data.projectName"
      :visible.sync="dialogFlag"
      @close="handleClose"
      width="580px"
      :append-to-body="true"
      class="myDialog"
      center
    >
      <div class="content">
        <template v-if="dialog_data.id<5">
          <div class="des">
            <span>项目描述：</span>
            {{dialog_data.dialogDatas.des}}
          </div>
          <div class="function">
            <span>实现功能：</span>
            <div v-for="item in dialog_data.dialogDatas.function" :key="item.id">
              <svg-icon iconClass='bullet'></svg-icon>
              {{item}}
              </div>
          </div>
        </template>
        <template v-else>
          <div>{{dialog_data.dialogDatas}}</div>
        </template>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Dialog",
  props: {
    flag: {
      type: Boolean
    },
    dialog_data: {
      type: Object
    }
  },
  data() {
    return {
      dialogFlag: false
    };
  },
  components: {},
  mounted() {},
  methods: {
    handleClose() {
      this.dialogFlag = false;
      this.$emit("update:flag", false);
    }
  },
  watch: {
    flag(nval, oval) {
      this.dialogFlag = nval;
    }
  }
};
</script>

<style lang='less' scoped>
.myDialog /deep/ .el-dialog {
  .content {
    padding: 30px;
    padding-top: 0;
    line-height: 25px;
    font-size: 14px;
    span {
      font-weight: bold;
    }
    .function{
      line-height: 30px;
      svg{
        font-size: 12px;
        margin-right: 10px;
      }
    }
  }
}
</style>